html, body {
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Source Sans 3', sans-serif;
    background-color:#f1ecf3;
}


/* Header */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

header {
    color: white;
    text-align: center;
    font-size: calc(10px + 2vmin);
    background-image: url('https://i.pinimg.com/736x/8a/20/4a/8a204a6a60fdef24baa2f05b05ebd743.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed; /* Keeps the background fixed while scrolling */
    background-repeat: no-repeat; /* Prevents tiling of the image */
    min-height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    animation: changeBackground 15s ease-in-out infinite; /* Animation for changing backgrounds */
}

/* Gradient Overlay */
header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5); /* Adds dark overlay */
    z-index: 1; /* Ensures the overlay is beneath the text */
}

/* Header text */
header h1.custom-font {
    font-size: 52px;
    font-family: 'Raleway', serif;
    font-weight: bold;
    color: #FFFFFF; /* White color for better contrast on the overlay */
    margin-bottom: 10px;
    z-index: 2; /* Ensures the header text stays on top of the overlay */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Adds a shadow for better readability */
}

/* Subheading text */
header p {
    font-size: 20px;
    line-height: 0.8;
    color: #FFFFFF; /* White for better contrast */
    margin: 10px;
    font-weight: bold;
    z-index: 2; /* Ensures the paragraph stays on top */
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.7); /* Adds a subtle shadow effect */
}

/* Animation for changing background images */
@keyframes changeBackground {
    0% {
        background-image: url('https://i.pinimg.com/736x/37/5e/04/375e040e982083a2ec1f5d2ee36c928d.jpg');
    }
    33% {
        background-image: url('https://www.jomjalan.com/wp-content/uploads/2016/12/penanghill-1.jpg');
    }
    66% {
        background-image: url('https://heartpatrick.com/penang_bridge_aerial_photography/heartpatrick_street_aerial_drone_photography_penang_bridge13.jpg');
    }
    100% {
        background-image: url('https://heartpatrick.com/penang10/heartpatrick_penang_pulau_pinang_georgetown_malaysia19.jpg');
    }
}



/* Navigation Block Styling */
.nav-blocks {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
    padding: 20px;
    background-color: #ffffff;
}

.nav-block {
    display: inline-block;
    background-color: #f1f1f1;
    color: #333;
    padding: 15px 20px;
    border: 2px solid transparent;
    border-radius: 8px;
    text-align: center;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    transition: all 0.3s ease;
    min-width: 120px;
}

.nav-block:hover {
    background-color: #dcdcdc;
    transform: scale(1.05);
}

.nav-block.active {
    background-color: #45a049;
    color: #ffffff;
    border-color: #3b8c40;
}




/* Content Styling */
h1 {
    font-size: 24px;
}


/* Dynamic Content Styles */
@keyframes slideIn {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: translateX(0);
    }
}

#dynamic-content {
    margin: 40px auto;
    max-width: 500px;
    padding: 15px;
    border-radius: 12px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: slideIn 0.8s ease-out;
}

/* Image and Text Pairing */
.image-text-pair {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    padding: 10px;
    border-radius: 12px;
    background-image: url('https://images.pexels.com/photos/10526919/pexels-photo-10526919.jpeg?auto=compress&cs=tinysrgb&w=600');
    background-size: cover;
    background-position: center;
    color: #000000;
    transition: all 0.3s ease;
}

.image-text-pair:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.image-text-pair img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}

.image-text-pair .text {
    flex: 1;
    font-size: 30px;
    line-height: 1.6;
    text-align: left;
}

.image-text-pair .text h2 {
    font-size: 30px;
    margin-bottom: 10px;
    font-weight: 700;
}

.image-text-pair + .image-text-pair {
    border-top: 2px solid #ddd;
    padding-top: 20px;
    margin-top: 20px;
}

/* Optional: Add styling to the content inside */
.text p {
    margin-bottom: 15px;
    font-size: 25px;
}

#dynamic-content .heading {
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
}



.about-us-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    color: #333;
    text-align: center;
}

.about-us-container h1 {
    font-size: 2.5em;
    color: #222;
    margin-bottom: 20px;
}

.about-us-container p {
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 30px;
    color: #666;
}

.about-image {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-top: 40px;
    text-align: left;
}

.about-image-img {
    width: 100%;
    max-width: 400px;
    height: auto;
    border-radius: 8px;
}

.about-description {
    max-width: 500px;
    font-size: 1.1em;
    color: #444;
}

.about-description h3 {
    font-size: 1.5em;
    color: #333;
    margin-bottom: 15px;
}

.about-description p {
    font-size: 1.1em;
    color: #666;
    line-height: 1.6;
}


/* Footer */
@keyframes footerFadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

footer {
    background-color: white;
    color: #000000;
    text-align: center;
    padding: 10px;
    width: 100%;
    animation: footerFadeIn 1s ease-in-out;
}

/* Media Queries for Responsiveness */
@media screen and (max-width: 1024px) {
    header h1.custom-font {
        font-size: 2.5rem;
    }

    .image-text-pair {
        flex-direction: column;
        text-align: center;
    }

}

@media screen and (max-width: 768px) {

    .image-text-pair {
        flex-direction: column;
    }

    header h1 {
        font-size: 2rem;
    }

    header p {
        font-size: 1.2rem;
    }
}

@media screen and (max-width: 480px) {
    header h1 {
        font-size: 1.5rem;
    }


    .about-us-container h1 {
        font-size: 2em;
    }

    .about-us-container p {
        font-size: 1em;
    }
}

@media screen and (max-width: 768px) {
    .about-image {
        flex-direction: column; /* Stacks items vertically on small screens */
    }

    .about-image-img {
        max-width: 80%; /* Makes the image take up more width on small screens */
    }

    .about-description {
        max-width: 90%; /* Adjusts the text block width for smaller screens */
    }
}

@media screen and (max-width: 768px) {
    header {
        min-height: 60vh; /* Reduces height for smaller screens */
        font-size: calc(8px + 2vmin); /* Adjusts font size dynamically */
    }
}

/* Media Query for Very Small Screens */
@media screen and (max-width: 480px) {
    header {
        min-height: 50vh; /* Further reduces height for very small screens */
        font-size: calc(6px + 2vmin); /* Scales font size down */
        padding: 10px; /* Reduces padding for better layout */
    }
}

/* Responsive Navigation Blocks */
@media (max-width: 768px) {
    .nav-block {
        font-size: 14px;
        padding: 10px 15px;
    }
}

@media (max-width: 480px) {
    .nav-block {
        font-size: 12px;
        padding: 8px 10px;
    }
}